

























import { Component, Vue } from "vue-property-decorator";
import { IArea, IAlbum } from "../types";
import AlbumItem from "./AlbumItem.vue";
import * as request from "../request";

class Area implements IArea {
  id: number;
  name: string;

  constructor(obj: IArea) {
    this.id = obj.id;
    this.name = obj.name;
  }
}

class Album implements IAlbum {
  id: number;
  area: number;
  name: string;
  singer: string;
  release_time: string;
  cover: string;

  constructor(obj: IAlbum) {
    this.id = obj.id;
    this.area = obj.area;
    this.name = obj.name;
    this.singer = obj.singer;
    this.release_time = obj.release_time;
    this.cover = obj.cover;
  }
}

@Component({
  components: {
    AlbumItem,
  },
})
export default class AlbumList extends Vue {
  areas: Area[] = [];
  albums: Album[] = [];
  currentArea = 0;
  async load() {
    const [_areas, _albums] = await Promise.all([
      request.get<IArea[]>("data/areas.json"),
      request.get<IAlbum[]>("data/albums.json"),
    ]);
    this.areas = _areas.map((obj) => new Area(obj));
    this.albums = _albums.map((obj) => new Album(obj));
    // 设置当前选中第一个tab
    this.currentArea = this.areas[0].id;
  }
  changeTab(id: number) {
    this.currentArea = id;
  }
  deleteAlbum(album: IAlbum) {
    const index = this.albums.indexOf(album);
    this.albums.splice(index, 1);
  }
  get currentAlbums() {
    return this.albums.filter((album) => album.area === this.currentArea);
  }
  created() {
    this.load();
  }
}
